@charset "UTF-8";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
}

a {
  text-decoration: none;
}
a:active, a:hover {
  outline: 0;
}

ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

img,
audio,
video {
  max-width: 100%;
  height: auto;
}

audio,
canvas,
iframe,
video,
img,
svg {
  vertical-align: middle;
}

iframe {
  border: 0;
}

textarea {
  resize: none; /*remove the resize handle on the bottom right*/
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

input,
textarea,
select,
button {
  outline: none;
  border: none;
  font-size: 100%;
  margin: 0;
}

button,
input {
  line-height: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

.clear {
  clear: both;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

html, body {
  font-family: "Noto Sans", sans-serif;
  line-height: 140%;
  padding: 0;
  margin: 0;
  font-weight: 400;
  color: rgb(255, 250, 250);
  position: relative;
}

body {
  font-size: 16px;
  background-color: rgb(248, 248, 248);
  height: 100vh;
  display: flex;
  flex-direction: column;
}
body.overflow {
  overflow-y: hidden;
}

.header, .footer {
  position: fixed;
  width: 100%;
  z-index: 2;
}

.header {
  top: 0;
  left: 0;
}

.footer {
  bottom: 0;
  left: 0;
}

* {
  box-sizing: border-box;
}

.container {
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  font-size: 16px;
  position: relative;
}

section {
  width: 100%;
}

.task-list {
  position: relative;
  padding: 90px 0;
  height: 100vh;
  overflow-y: auto;
  background-color: #1F2428;
}
.task-list__title {
  font-size: 14px;
  font-weight: 700;
  color: rgb(22, 22, 22);
  padding: 6px 8px 6px;
}
.task-list__title.hide {
  display: none;
}
.task-list__wrapper {
  display: flex;
  align-items: flex-start;
  overflow-x: auto;
  padding-bottom: 40px;
}
.task-list__inner {
  margin-right: 20px;
  position: relative;
  border-radius: 4px;
  padding-bottom: 12px;
  background-color: rgb(170, 165, 165);
  min-width: 280px;
  max-width: 280px;
}
.task-list__inner:hover .task-list__add-task {
  opacity: 1;
  padding: 6px 10px;
  height: auto;
  visibility: visible;
}
.task-list__track {
  margin-bottom: 12px;
  max-height: 60vh;
  overflow-y: auto;
}
.task-list__header {
  z-index: 2;
  width: 100%;
  border-radius: 4px;
  padding: 6px 10px;
  background-color: rgb(236, 228, 228);
  margin-bottom: 12px;
  color: rgb(22, 22, 22);
}
.task-list__add-task {
  transition: 0.25s;
  display: flex;
  opacity: 0;
  align-items: center;
  visibility: hidden;
  height: 0;
  padding: 0;
}
.task-list__add-task-btn {
  cursor: pointer;
  color: rgb(255, 250, 250);
  border: none;
  background-color: transparent;
}
.task-list__add-task-ico {
  width: 14px;
  height: 14px;
  background-color: #fff;
  mask-size: 100% 100%;
  mask-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='20' height='20' fill='none' viewBox='0 0 20 20'%3e%3cpath fill='%23000' d='M10.5 2.75a.75.75 0 1 0-1.5 0V9H2.75a.75.75 0 0 0 0 1.5H9v6.25a.75.75 0 1 0 1.5 0V10.5h6.25a.75.75 0 1 0 0-1.5H10.5V2.75Z'/%3e%3c/svg%3e ");
}
.task-list__btn-add-wrap-task {
  cursor: pointer;
  position: fixed;
  bottom: 72px;
  right: 30px;
  z-index: 10;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  transition: 0.25s;
}
.task-list__btn-add-wrap-task:hover {
  background-color: #fff;
}
.task-list__btn-add-wrap-task img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header {
  height: 60px;
  background-color: #24292E;
  width: 100%;
  border-bottom: 1px solid #1B1F23;
}
.header .container {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__ico {
  cursor: pointer;
  min-width: 36px;
  max-width: 36px;
}
.header__ico img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.header__profile {
  position: relative;
}
.header__auth {
  border-radius: 4px;
  background-color: rgb(255, 250, 250);
  position: absolute;
  z-index: 10;
  top: 100%;
  padding: 20px 12px;
  left: -120px;
  width: 120px;
  display: none;
}
.header__auth.is-active {
  display: block;
}
.header__btn {
  cursor: pointer;
  margin-bottom: 12px;
  padding: 8px 12px;
  color: rgb(255, 250, 250);
  border-radius: 4px;
  width: 100%;
  transition: 0.25s;
}
.header__btn--profile {
  background-color: #9457EB;
}
.header__btn--profile:hover {
  background-color: #a66df7;
}
.header__btn--out {
  background-color: #FF5252;
}
.header__btn--out:hover {
  background-color: #fa6b6b;
}
.header__btn:last-child {
  margin-bottom: 0;
}

.footer {
  background-color: #24292E;
  height: 60px;
  border-top: 1px solid #1B1F23;
}

.task {
  border-radius: 4px;
  color: rgb(22, 22, 22);
  background-color: rgb(255, 250, 250);
  margin: 0 6px 12px;
}
.task:last-child {
  margin-bottom: 0;
}
.task__body {
  padding: 6px 10px;
}
.task__sub-title {
  font-size: 16px;
  font-weight: 500;
}
.task__preview {
  position: relative;
  padding-right: 30px;
}
.task__preview:hover .task__btn-edit-task {
  display: block;
}
.task__btn-edit-task {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 4px;
  transform: translateY(-50%);
  background-color: rgb(80, 63, 63);
  width: 20px;
  height: 20px;
  border: 1px solid gray;
  mask-size: 100% 100%;
  mask-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' fill='none' viewBox='0 0 24 24'%3e%3cpath stroke='%23000' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m16.474 5.408 2.118 2.117m-.756-3.982L12.109 9.27a2.118 2.118 0 0 0-.58 1.082L11 13l2.648-.53c.41-.082.786-.283 1.082-.579l5.727-5.727a1.854 1.854 0 0 0-2.621-2.621v0Z'/%3e%3cpath stroke='%23000' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M19 15v3a2 2 0 0 1-2 2H6a2 2 0 0 1-2-2V7a2 2 0 0 1 2-2h3'/%3e%3c/svg%3e ");
}

/* W3C standard
   сейчас только для Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(36, 41, 46, 0.4431372549) rgba(62, 68, 75, 0.4431372549);
}

/* для Chrome/Edge/Safari */
*::-webkit-scrollbar {
  height: 8px;
  width: 6px;
}

*::-webkit-scrollbar-track {
  background: none;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(36, 41, 46, 0.4431372549);
  border-radius: 3px;
  border: 1px solid transparent;
}

.input {
  display: none;
}
.input.is_active {
  display: block;
}
.input input {
  padding: 6px 12px;
  font-size: 16px;
  font-family: "Noto Sans", sans-serif;
  border-radius: 4px;
}